<template>
  <div class="note__item">
    <div class="note__item-top" :class="{ between: !grid }">
      <h3 class="note__item-title">{{ name.title }}</h3>
      <p class="note__item-date">{{ name.date }}</p>
    </div>
    <p class="note__item-text">
      {{ name.descr }}
    </p>
    <div class="note__item-btns">
      <button class="note__item-btn edit" @click="$emit('changeNote', name.id)">
        <img src="@/assets/img/edit.svg" alt="" />
        <span>{{words.editbtn[lang]}}</span>
      </button>
      <button class="note__item-btn del" @click="$emit('delNote', name.id)">
        <img src="@/assets/img/del.svg" alt="" />
        <span>{{words.deledit[lang]}}</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    grid: Boolean,
    name: Object,
    lang: String,
  },
  inject:['words'],
};
</script>

